import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { HttpEventType, HttpEvent } from '@angular/common/http';
import { IsLoadingService } from '@service-work/is-loading';
import { Observable } from 'rxjs';
import {
  AuthService,
  NotificationsService,
  elementAnimations
} from '../../shared';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss'],
  animations: [elementAnimations],
})
export class RegisterComponent implements OnInit {
	preview: string;
  percentDone: any = 0;
  hideP = true;
  regForm: FormGroup;

  constructor(
    public router: Router,
    public authService: AuthService,
    private isLoadingService: IsLoadingService,
    public notification: NotificationsService
  ) {
    this.regForm = new FormGroup({
      name: new FormControl('', [Validators.required, Validators.maxLength(30)]),
      username: new FormControl('', [Validators.required, Validators.minLength(2), Validators.maxLength(13)]),
      email: new FormControl('', [Validators.required, Validators.maxLength(30)]),
      password: new FormControl('', [Validators.required]),
      avatar: new FormControl(null, [Validators.required])
    })
  }

  ngOnInit() {}

  // Select avatar
  onFileSelected(event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.regForm.patchValue({avatar: file});
		this.regForm.get('avatar').updateValueAndValidity()

		// File Preview
    const reader = new FileReader();
    reader.onload = () => {
      this.preview = reader.result as string;
    }
    reader.readAsDataURL(file)
  }

  submitForm() {
    this.isLoadingService.add();
    this.regForm.disable();
		this.authService.registerUser(
			this.regForm.value.name,
			this.regForm.value.username,
			this.regForm.value.email,
			this.regForm.value.password,
			this.regForm.value.avatar
		)
		.subscribe((event: HttpEvent<any>) => {
      switch (event.type) {
        case HttpEventType.UploadProgress:
          this.percentDone = Math.round((event.loaded / event.total) * 100);
          console.log(`Uploading! ${this.percentDone}%`);
          break;
        case HttpEventType.Response:
          if (event.body.success === false) {
            this.notification.error(event.body.message);
            this.regForm.enable();
          } else if (event.body.success === true) {
            this.notification.success(event.body.message);
            this.router.navigate(["esport/all-teams"]);
            this.isLoadingService.remove();
          }
      }
    })
    console.log(this.regForm.value);
  }

}
